import React, {Component} from 'react';
import './style.less'

// 
export default class extends Component {
    constructor(props) {
        super(props)
    }
    static defaultProps = {
        brand: '',
        price: '',
        img: '',
        isNew: false,
        href: ''
    }
    render() {
        const {brand, price, img, isNew, href} = this.props
        return (
            <a className="brand-item" href={href}>
                <img src={img} />
                <div className="content" >
                    <p className="text">
                        {brand}
                    </p>
                    <p className="text">
                        {price}
                    </p>
                    {isNew ? <i className="icon-new" /> : ''}
                </div>
            </a>
        )
    }
}


